@import url(https://fonts.googleapis.com/css?family=Muli);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.filter-row {
  margin-bottom: 16px;
}

.mobile-group-title {
  color: #666666;
  font-size: 12px;
  margin-bottom: 0px;
}

.filter-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.control-button {
  color: #666666;
  font-size: 12px;
  font-weight: bold;
  margin: 8px 0px;
}
.control-button:not(:last-child) {
  margin-right: 12px;
}
.green {
  color: #16ab39;
}


.bottom-button > .ui.attached.button {
	box-shadow: none!important;
}

.ui.card>extra, .ui.cards>.card>.extra {
	border-top: none!important;
}

.ui.card>:first-child, .ui.cards>.card>:first-child {
	border-radius: 10px 10px 0px 0px!important;
}

.ui.card, .ui.cards>.card {
	width: 260px;
}

.banner-logo {
	height: 72px;
	top: 4px;
	left: 4px;
	position: absolute;
}

#report-button>button {
	box-shadow: none!important;
	padding:4px 0px 0px 4px!important;
}

#report-button i:hover{
	color: #db2828!important;
}

/* Mobile */
@media only screen and (max-width: 801px) {
	.ui.card, .ui.cards>.card {
		width: 100%;
	}

	.banner-logo {
		height: 72px;
		top: 4px;
		left: 4px;
		position: absolute;
	}
}
.filter-search-container {
    display: flex;
    flex-direction: row;
}

.mobile-search-bar {
    flex-grow: 1;
}
.b1{
  position: relative;
  display: inline-block;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover !important;
	-webkit-background-size: cover !important;
  background-image: url(/static/media/bg.ab998653.jpg);
  width: 100%;
  height: 100vh;
  min-height: 680px;
  text-align: center;
}

.helpbutton{
  line-height: 12px;
  font-size: 8pt;
  font-family: tahoma;
  margin-top: 5px;
  margin-right: 2%;
  position:absolute;
  top:0;
  right:0;
}

.covidbutton{
  line-height: 12px;
  font-size: 8pt;
  font-family: tahoma;
  margin-top: 5px;
  margin-left: 2%;
  position:absolute;
  top:0;
  left:0;
}

.stuff{
  display: inline-block;
  height: 100%;
  padding-top: 40px;
  max-width: 800px;
  width: 95%;
  margin: 0 auto;
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
  vertical-align: middle;
}

.titleImage {
  margin-top: calc(5px + (15 - 5) * ((100vw - 300px) / (1600 - 300)));
  width: 90%;
}

.stuff h4{
    font-size: large;
    position: center;
    font-family: 'Muli', sans-serif;
    font-weight:normal;
    color: white;
}


.stuff h3{
    font-size: large;
    position: center;
    font-family: 'Muli', sans-serif;
    font-weight: bold;
    font-size: 20px;
    color: white;
    padding-bottom: 0px;
    padding-top: 15px;
}

.guide{
  position: center;
  padding-bottom: calc(8px + (12 - 6) * ((100vw - 300px) / (1600 - 300)));
  padding-top: calc(12px + (18 - 6) * ((100vw - 300px) / (1600 - 300)));
  font-family: 'Muli', sans-serif;
  font-weight: bold;
  color: white;
  font-size: calc(15px + (28 - 15) * ((100vw - 300px) / (1600 - 300)));
  /* calc([minimum size] + ([maximum size] - [minimum size]) * ((100vw - [minimum viewport width]) / ([maximum viewport width] - [minimum viewport width]))) */
}

.select{
  padding-bottom: calc(12px + (18 - 6) * ((100vw - 300px) / (1600 - 300)));
  padding-top: calc(8px + (12 - 6) * ((100vw - 300px) / (1600 - 300)));
  font-size: calc(13px + (25 - 13) * ((100vw - 300px) / (1600 - 300)));

  position: center;
  font-family: 'Muli', sans-serif;
  font-weight: normal;
  color: white;
}

.ButtonClass{
  position: center;
  margin-top: 30px;
  -webkit-animation-delay: 2.5s;
}

.buttonsAll {
  display: flex; 
  justify-content: space-between;
}

.resourceButton1 {
  -webkit-animation-delay: 1s; 
}

.resourceButton2 {
  -webkit-animation-delay: 1.5s; 
}

.resourceButton3 {
  -webkit-animation-delay: 2s; 
}

/* screenwidth less than 1024px
--------------------------------------------------------------------- */
@media only screen and (max-width: 1024px) {
}

/* screenwidth less than 900px
--------------------------------------------------------------------- */
@media only screen and (max-width: 900px) {
}

/* mobile wide/smaller tablets
---------------------------------------------------------------------- */

@media only screen and (max-width: 767px) {
}

/* mobile narrow
-------------------------------------------------------------------------- */

@media only screen and (max-width: 600px) {
  .buttonsAll {
    display: block; 
  }

  .resourceButton1 {
    display:block;
    margin-bottom: 1em;
  }
  
  .resourceButton2 {
    display:block;
    margin-bottom: 1em;
  }
  
  .resourceButton3 {
    display:block;
  }
}
